  </div>
</template>

<script>
import { computed, onMounted, ref } from "@vue/runtime-core";
import Entradabit from "../components/Entradabit.vue";
import { useStore } from "vuex";

export default {
  components: { Entradabit },
  setup() {
    const store = useStore();

    const conexion = ref(false);

    const Swal = require("sweetalert2");
    const Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    onMounted(async () => {
      try {
        const response = await store.dispatch("getConexion", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "conexion",
        });

        if (response) {
          conexion.value = true;
          Swal.fire("ONLINE", "Conexión al Servidor!", "success");
        } else {
          conexion.value = false;
          Swal.fire("OFFLINE", "Sin Acceso a Internet!", "info");
        }
      } catch (error) {
        conexion.value = false;
      }
    });

    return { conexion };
  },
};
</script>

<style scoped>
.contbotones {
  scroll-snap-type: x mandatory;
  overflow-x: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  @apply w-full flex flex-row justify-center items-start px-2;
}

.contbotones::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}
.seccion {
  scroll-snap-align: center;
  flex: none;
  width: 100%;
}

@media (min-width: 768px) {
  .seccion {
    width: 33.333333%;
  }
}
</style>
